import { request } from 'utils';

const prefix = '/api/education';

export const getResgisterSessionSelect = ({
  trainTypeId,
  yearEduId,
  semesterId,
  fullFormat,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/RegisterSession/DataForSelect',
    method: 'GET',
    params: {
      trainTypeId,
      yearEduId,
      semesterId,
      fullFormat,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });



